// import useSWR from "swr";
import useSWRImmutable from "swr/immutable";
import fetchWithRetry from "fetch-retry";
import { retryConfig } from "../utils";

export const fetcher = (input: RequestInfo) =>
  fetchWithRetry(fetch)(input, { ...retryConfig }).then((res) => res.json());

export const useSWRNFetchImmutable = (api: string | string[] | null) =>
  useSWRImmutable(api, fetcher);

