import * as React from "react";
import TextField from "@mui/material/TextField";
import PickersDay from "@mui/lab/PickersDay";
import CalendarPickerSkeleton from "@mui/lab/CalendarPickerSkeleton";
import { useIntl, useTranslations } from "next-intl";
import MobileDatePicker from "@mui/lab/MobileDatePicker";
import { useRates } from "../hooks";
import Box from "@mui/material/Box";
import { useGlobal } from "../context";
import { captureException } from "@sentry/nextjs";
import fetchWithRetry from "fetch-retry";
import { retryConfig } from "../utils";

export function FlightDatePicker({
  label,
  onChange,
  value,
  from,
  to,
  rates,
  minDate = new Date(),
  maxDate = new Date(new Date().setMonth(new Date().getMonth() + 6)),
}: any) {
  const t = useTranslations();
  const [isOpen, setIsOpen] = React.useState(false);
  const { data: ratesFromApi, isLoading: isLoadingRates } = useRates({
    toFetch: !!from && !!to && isOpen,
  });
  const { currency } = useGlobal();
  const intl = useIntl();
  const ratesOr = React.useMemo(
    () => rates || ratesFromApi,
    [rates, ratesFromApi]
  );

  const requestAbortController = React.useRef<AbortController | null>(null);
  const [isLoading, setIsLoading] = React.useState(false);
  const [prices, setPrices] = React.useState<any>({});

  const fetchPrices = (date: Date) => {
    const controller = new AbortController();
    const month = new Date(Date.UTC(date.getFullYear(), date.getMonth(), 1))
      .toISOString()
      .split("T")[0];

    if (from && to && month && ratesOr) {
      setIsLoading(true);
      fetchWithRetry(fetch)(
        `https://lyssa.aviasales.ru/date_picker_prices?depart_months[]=${month}&destination_iata=${to.code}&one_way=true&origin_iata=${from.code}`,
        {
          signal: controller.signal,
          ...retryConfig
        }
      )
        .then((r) => r.json())
        .then((res: any) => {
          if (res.prices?.length) {
            const pricesLength = res.prices.length;
            const pricesByDay = {} as any;

            res.prices
              .sort((a: any, b: any) => a.price - b.price)
              .forEach(({ price, depart_date }: any, index: number) => {
                pricesByDay[
                  // depart_date?.split("-")[2] // ?.replace(/^0/g, "") - 1
                  String(new Date(depart_date).getDate())
                ] = {
                  price,
                  isLow:
                    pricesLength > 5 && index <= Math.round(pricesLength / 4),
                };
              });

            setPrices(pricesByDay);
          }
          setIsLoading(false);
        })
        .catch((error) => {
          setIsLoading(false);
          // ignore the error if it's caused by `controller.abort`
          if (error?.name !== "AbortError") {
            captureException(error)
          }
        });
      requestAbortController.current = controller;
    }
  };

  React.useEffect(() => {
    if (isOpen && value && !isLoadingRates) {
      fetchPrices(value);
      // abort request on unmount
      return () => requestAbortController.current?.abort();
    }
  }, [isOpen, isLoadingRates]);

  const handleMonthChange = (date: Date) => {
    if (requestAbortController.current) {
      // make sure that you are aborting useless requests
      // because it is possible to switch between months pretty quickly
      requestAbortController.current.abort();
    }

    setPrices({});
    fetchPrices(date);
  };

  return (
    <MobileDatePicker
      showToolbar={false}
      label={label}
      views={["day"]}
      orientation="portrait"
      value={value}
      loading={isLoading}
      minDate={minDate}
      maxDate={maxDate}
      onOpen={() => setIsOpen(true)}
      onClose={() => setIsOpen(false)}
      renderLoading={() => <CalendarPickerSkeleton />}
      disableHighlightToday
      onChange={(newValue: Date | null) => {
        onChange(newValue);
      }}
      onMonthChange={handleMonthChange}
      renderInput={(params) => <TextField {...params} InputLabelProps={{ shrink: true }} size="small" />}
      renderDay={(day, _value, DayComponentProps) => {
        const priceForDay = prices[String(day.getDate())] as any;
        const priceFormatted =
          ratesOr &&
          priceForDay &&
          intl.formatNumber(
            priceForDay.price / ratesOr[currency.code.toLowerCase()],
            {
              style: "currency",
              currency: currency.code,
              maximumFractionDigits: 0,
              minimumFractionDigits: 0,
            }
          );

        return (
          <Box
            key={day.toString()}
            sx={{
              position: "relative",
              display: "flex",
              justifyContent: "center",
            }}
          >
            {/* <Badge
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "right",
              }}
              color="secondary"
              badgeContent={priceForDay && !DayComponentProps.outsideCurrentMonth && priceFormatted}
            > */}
            <PickersDay {...DayComponentProps} today={false} />
            {/* </Badge> */}
            {priceForDay && !DayComponentProps.outsideCurrentMonth && (
              <Box
                sx={{
                  position: "absolute",
                  bottom: "-6px",
                  fontSize: ".5625rem",
                  lineHeight: "1.2",
                  color: priceForDay.isLow ? "#35c768" : "#9ea9b7",
                  zIndex: 0,
                  borderRadius: "10px",
                  // background: '#fff',
                  padding: "3px",
                }}
              >
                {priceFormatted}
              </Box>
            )}
          </Box>
        );
      }}
    />
    // <LocalizationProvider dateAdapter={AdapterDateFns}>
    //   <DatePicker
    //     value={value}
    //     loading={isLoading}
    //     onChange={(newValue) => {
    //       setValue(newValue);
    //     }}
    //     onMonthChange={handleMonthChange}
    //     renderInput={(params) => <TextField {...params} />}
    //     renderLoading={() => <CalendarPickerSkeleton />}
    //     renderDay={(day, _value, DayComponentProps) => {
    //       const isSelected =
    //         !DayComponentProps.outsideCurrentMonth &&
    //         highlightedDays.indexOf(day.getDate()) > 0;

    //       return (
    //         <Badge
    //           key={day.toString()}
    //           overlap="circular"
    //           badgeContent={isSelected ? 'ðŸŒš' : undefined}
    //         >
    //           <PickersDay {...DayComponentProps} />
    //         </Badge>
    //       );
    //     }}
    //   />
    // </LocalizationProvider>
  );
}
