import React from "react";
import { trackEvent } from "../utils";
import Link from "./Link";

const footers = [
  {
    title: "footer-booking",
    description: [
      { name: "flights", link: "routes-flights" },
      { name: "navbar-hotels", externalLink: "https://tp.media/r?marker=56123&trs=15329&p=2076&u=https%3A%2F%2Fbooking.com" },
      { name: "navbar-cars", externalLink: "https://tp.media/r?marker=56123&trs=15329&p=3814&u=https%3A%2F%2Frentalcars.com" },
      // { name: "explore", link: "routes-explore" },
    ],
  },
  {
    title: "info-pages",
    description: [
      { name: "airlines", link: "routes-airlines" },
      { name: "airports", link: "routes-airports" },
      { name: "cities", link: "routes-cities" },
      { name: "countries", link: "routes-countries" },
      { name: "baggage-fees", link: "routes-airlines-baggage-fees" },
      { name: "cheap-flights", link: "routes-cheap-flights" },
    ],
  },
  {
    title: "company",
    description: [
      { name: "privacy", link: "routes-privacy-policy" },
      { name: "terms", link: "routes-terms-and-conditions" },
      { name: "cookies", link: "routes-cookies" },
    ],
  },
  {
    title: "footer-connect",
    description: [
      {
        name: "footer-facebook",
        externalLink: "https://www.facebook.com/flightscannercom",
      },
      {
        name: "footer-twitter",
        externalLink: "https://twitter.com/flightscanner",
      },
      {
        name: "footer-instagram",
        externalLink: "https://instagram.com/flightscannercom",
      },
    ],
  },
] as any;

export const Footer = () => {
  const t = useTranslations();
  return (
    <Box component="footer">
      <Divider />
      <Container maxWidth="xl" sx={{ py: 2 }}>
        <Box sx={{ display: "flex", flexWrap: "wrap" }}>
          {footers.map((footer: any) => (
            <Box key={footer.title} sx={{ padding: 1, flex: 1 }}>
              <Typography variant="h6" color="textPrimary" gutterBottom>
                {t(footer.title)}
              </Typography>
              {footer.description.map((item: any) => (
                <Link
                  key={item.name}
                  target={item.externalLink && "_blank"}
                  href={
                    item.externalLink
                      ? `${item.externalLink}`
                      : `/${t(item.link)}`
                  }
                  underline="hover"
                  color="inherit"
                  onClick={() => {
                    trackEvent({
                      action: "footerLink",
                      label: item.name,
                    });
                  }}
                >
                  <Typography variant="subtitle1" color="textSecondary">
                    {t(item.name)}
                  </Typography>
                </Link>
              ))}
            </Box>
          ))}
        </Box>
      </Container>
      <Divider />
      <Box sx={{ display: "flex", justifyContent: "center", p: 1 }}>
        {t("footer-copyright", { value: new Date().getFullYear() })}
      </Box>
    </Box>
  );
};
