import { useSWRNFetchImmutable } from "./useSwr";

type useRatesType = {
  toFetch?: boolean;
};

export const useRates = ({ toFetch = undefined }: useRatesType = {}) => {
  const { data, error } = useSWRNFetchImmutable(
    toFetch === undefined || toFetch === true
      ? `https://apiv2.flightscanner.com/rates`
      : ""
  );

  return {
    data,
    isLoading: !error && !data,
    isError: error,
  };
};
