import React, { useState, useMemo, useEffect, useRef } from "react";
import { useTranslations } from "next-intl";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import {
  DateRangePicker,
} from "@mui/x-date-pickers-pro/DateRangePicker";
import TextField from "@mui/material/TextField";

import { LocalizationProvider } from "@mui/x-date-pickers-pro";
import { AdapterDateFns } from "@mui/x-date-pickers-pro/AdapterDateFns";

import Container from "@mui/material/Container";
import MenuItem from "@mui/material/MenuItem";
import Stack from "@mui/material/Stack";

import ArrowDropDown from "@mui/icons-material/ArrowDropDown";
import Person from "@mui/icons-material/Person";
import Search from "@mui/icons-material/Search";

import SearchLocation from "./SearchLocation";
import Button from "@mui/material/Button";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import CounterInput from "./CounterInput";
import { useRouter } from "next/router";
import { useGlobal } from "../context";
import { navigateToSearchResults, trackEvent } from "../utils";
import { FlightDatePicker } from "./FlightDatePicker";
import { useWhoami } from "../hooks";
import Divider from "@mui/material/Divider";
import { MuiPickersAdapterContext } from "@mui/lab/LocalizationProvider/LocalizationProvider";
// import Link from "@mui/material/Link";
// import Chip from "@mui/material/Chip";

export enum ETripTypes {
  round = "round",
  oneWay = "oneWay",
  multiCity = "multiCity",
}

export enum ESuggestionsTypes {
  cheap = "Cheap",
  popular = "Popular",
}

export enum StopsTypes {
  nonStop = "NonStop",
  oneStop = "OneStop",
  twoStops = "TwoStop",
}

export enum ETripClass {
  economy = "Y",
  business = "C",
  first = "F",
}

const tripTypes = [
  { id: ETripTypes.round, value: "Round trip", i18n: "search-form-round" },
  { id: ETripTypes.oneWay, value: "One-way", i18n: "search-form-one-way" },
];

const tripClasses = [
  { id: ETripClass.economy, i18n: "search-form-economy" },
  { id: ETripClass.business, i18n: "search-form-business" },
  // { id: ETripClass.first, i18n: "search-form-first" },
];

const suggestionsTypes = [
  { id: ESuggestionsTypes.cheap, value: "Cheap", i18n: "suggestions-cheap" },
  {
    id: ESuggestionsTypes.popular,
    value: "Popular",
    i18n: "suggestions-popular",
  },
];

const stopsTypes = [
  {
    id: StopsTypes.nonStop,
    value: "NonStop",
    title: "Nonstop only",
    i18n: "filters-stops-non-stop",
  },
  {
    id: StopsTypes.oneStop,
    value: "OneStop",
    title: "1 stop or fewer",
    i18n: "filters-stops-one-stop",
  },
  {
    id: StopsTypes.twoStops,
    value: "TwoStop",
    title: "2 stops or fewer",
    i18n: "filters-stops-two-stop",
  },
];

const DatePickerWithLoclae = ({
  value = [null, null] as any,
  onChange = (values: any) => { },
}) => {
  const t = useTranslations();
  const { dateLocaleFile } = useGlobal();

  return (
    <LocalizationProvider
      dateAdapter={AdapterDateFns}
      localeText={{ start: t("departure-date-?"), end: t("return-date-?") }}
      adapterLocale={dateLocaleFile}
    >
      <DateRangePicker
        value={value}
        minDate={new Date()}
        maxDate={new Date(new Date().setMonth(new Date().getMonth() + 6))}
        onChange={(newValue) => {
          // setRange(newValue);
          onChange(newValue);
        }}
        renderInput={(startProps, endProps) => (
          <Box sx={{ display: 'flex', flex: 1}}>
            <TextField
              {...startProps}
              InputLabelProps={{ shrink: true }}
              size="small"
              sx={{ flex: 1}}
            />
            <TextField
              {...endProps}
              InputLabelProps={{ shrink: true }}
              size="small"
              sx={{ ml: 2, flex: 1 }}
            />
          </Box>
        )}
      />
    </LocalizationProvider>
  );
};

const SearchForm = ({
  location,
  showBooking = true,
  maxWidth = "xl",
  rates,
  shouldFetch = false,
  initalFrom,
  initialTo,
}: any) => {
  const t = useTranslations();
  const { query, push, replace } = useRouter();
  const { setSearchState } = useGlobal();
  const { data: userLocation } = useWhoami({ shouldFetch });
  const setLocationOnce = useRef<boolean>(false);

  const [tripType, setTripType] = useState<typeof tripTypes[0]>(tripTypes[0]);
  const [tripClass, setTripClass] = useState<typeof tripClasses[0]>(
    tripClasses[0]
  );

  const [adults, setAdults] = useState(1);
  const [children, setChildren] = useState(0);
  const [infants, setInfants] = useState(0);

  const [from, setFrom] = useState<any>(initalFrom);
  const [to, setTo] = useState<any>(initialTo);

  const [initFromQuery, setInitFromQuery] = useState(false);

  const [departureDate, setDepartureDate] = useState<Date | null>(
    new Date(new Date().setDate(new Date().getDate() + 16))
  );
  const [returnDate, setReturnDate] = useState<Date | null>(
    new Date(new Date().setDate(new Date().getDate() + 21))
  );

  const [redirectToBooking, setRedirectToBooking] = useState(true);

  // Store prev state
  const prevPassengers = useRef<number[]>([adults, children, infants]);

  useEffect(() => {
    if (location && !from) {
      setFrom({
        ...location,
        city_code: location.iata,
        code: location.iata,
        city_name: location.name,
        country_name: location.country_name,
        _type: "city",
      });
    } else if (
      from?.code === location?.iata &&
      !setLocationOnce.current &&
      userLocation
    ) {
      setFrom({
        ...userLocation,
        city_code: userLocation.iata,
        code: userLocation.iata,
        city_name: userLocation.name,
        country_name: userLocation.country_name,
        forceUpdate: true,
        _type: "city",
      });
      setLocationOnce.current = true;
    }
  }, [location, from, userLocation]);

  useEffect(() => {
    if (query && query.from && query.to && !initFromQuery) {
      const fromSplit = decodeURIComponent(query.from as string)?.split(":");
      const toSplit = decodeURIComponent(query.to as string)?.split(":");
      setFrom({
        city_code: fromSplit[0],
        code: fromSplit[0],
        city_name: fromSplit[1],
        country_name: fromSplit[2],
        _type: "city",
      });
      setTo({
        city_code: toSplit[0],
        code: toSplit[0],
        city_name: toSplit[1],
        country_name: toSplit[2],
        _type: "city",
      });
      setTripType(query.returnDate === "none" ? tripTypes[1] : tripTypes[0]);
      setTripClass(
        query.tripClass
          ? tripClasses.find((t) => t.id === query.tripClass) || tripClasses[0]
          : tripClasses[0]
      );
      setAdults(Number(query.adults) || 1);
      setChildren(Number(query.children) || 0);
      setInfants(Number(query.infants) || 0);
      setDepartureDate(new Date(query.departureDate as string));
      if (query.returnDate === "none") {
        setReturnDate(null);
      } else {
        setReturnDate(new Date(query.returnDate as string));
      }

      setRedirectToBooking(false);
      setInitFromQuery(true);
    }
  }, [query, initFromQuery]);

  // Sync to globa state
  useEffect(() => {
    setSearchState({ from, to, departureDate, returnDate });
  }, [from, to, departureDate, returnDate, setSearchState]);

  // Trip
  const [anchorElTrip, setAnchorElTrip] = useState<null | HTMLElement>(null);

  const handleOpenTripMenu = async (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElTrip(event.currentTarget);
    trackEvent({
      action: "handleOpenTripMenu",
    });
  };

  const handleCloseTripMenu = () => {
    setAnchorElTrip(null);
    trackEvent({
      action: "handleCloseTripMenu",
    });
  };

  const handleTripChange = (trip: any) => {
    setTripType(trip);

    if (trip.id === ETripTypes.round) {
      setReturnDate(new Date(new Date().setDate(new Date().getDate() + 21)));
    } else {
      setReturnDate(null);
    }

    trackEvent({
      action: "handleTripChange",
      label: trip.id,
    });

    handleCloseTripMenu();
  };

  // Passengers
  const [anchorElPassengers, setAnchorElPassengers] =
    useState<null | HTMLElement>(null);

  const handleOpenPassengersMenu = async (
    event: React.MouseEvent<HTMLElement>
  ) => {
    prevPassengers.current = [adults, children, infants];
    setAnchorElPassengers(event.currentTarget);
  };

  const handleClosePassengersMenu = () => {
    setAnchorElPassengers(null);
    trackEvent({
      action: "handleClosePassengersMenu",
    });
  };

  const handleCancelPassengersMenu = () => {
    setAdults(prevPassengers.current[0]);
    setChildren(prevPassengers.current[1]);
    setInfants(prevPassengers.current[2]);
    setAnchorElPassengers(null);
    trackEvent({
      action: "handleCancelPassengersMenu",
    });
  };

  // Type
  const [anchorElType, setAnchorElType] = useState<null | HTMLElement>(null);

  const handleOpenTypeMenu = async (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElType(event.currentTarget);
    trackEvent({
      action: "handleOpenTypeMenu",
    });
  };

  const handleCloseTypeMenu = () => {
    setAnchorElType(null);
    trackEvent({
      action: "handleCloseTypeMenu",
    });
  };

  const handleTypeChange = (tripC: any) => {
    setTripClass(tripC);
    trackEvent({
      action: "handleTypeChange",
      label: tripC.id,
    });
    handleCloseTypeMenu();
  };

  const startSearch = () => {
    const searchState = {
      tripType,
      tripClass,
      adults,
      children,
      infants,
      from,
      to,
      departureDate,
      returnDate,
      redirectToBooking,
    };

    navigateToSearchResults({
      searchState,
      t,
      push,
      replace,
      query,
    });
    trackEvent({
      action: "startSearch",
    });
    // Recent destination
    if (localStorage && localStorage.getItem && localStorage.setItem) {
      const prevDestination = JSON.parse(
        localStorage.getItem("destination") || "[]"
      );
      localStorage.setItem(
        "destination",
        JSON.stringify([{ ...searchState }].concat(prevDestination).slice(0, 3))
      );
    }
  };

  const handleSearch = (e: any) => {
    e.preventDefault();
    if (from && to) {
      startSearch();
    }
  };

  const totalPassengers = useMemo(() => {
    return adults + children + infants;
  }, [adults, children, infants]);

  return (
    <Container maxWidth={maxWidth}>
      <Box sx={{ display: "inline-block", my: 1 }}>
        <Button
          aria-label="menu of trip type"
          aria-controls="menu-trip-type"
          aria-haspopup="true"
          color="inherit"
          onClick={handleOpenTripMenu}
        >
          <Typography variant="button" textAlign="center">
            {tripType && t(tripType.i18n)}
          </Typography>
          <ArrowDropDown />
        </Button>
        <Menu
          id="menu-trip-type"
          anchorEl={anchorElTrip}
          keepMounted
          open={Boolean(anchorElTrip)}
          onClose={handleCloseTripMenu}
        >
          {tripTypes?.map((trip) => (
            <MenuItem key={trip.id} onClick={() => handleTripChange(trip)}>
              <Typography textAlign="center">{t(trip.i18n)}</Typography>
            </MenuItem>
          ))}
        </Menu>
      </Box>

      <Box sx={{ display: "inline-block" }}>
        <Button
          size="large"
          aria-label="menu of passengers"
          aria-controls="menu-passengers"
          aria-haspopup="true"
          color="inherit"
          onClick={handleOpenPassengersMenu}
        >
          {totalPassengers}
          <Person />
          <ArrowDropDown />
        </Button>
        <Menu
          id="menu-passengers"
          anchorEl={anchorElPassengers}
          keepMounted
          open={Boolean(anchorElPassengers)}
          onClose={handleClosePassengersMenu}
          sx={{ userSelect: "none" }}
        >
          <Box sx={{ px: 2 }}>
            <Box
              sx={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                mb: 1,
              }}
            >
              <Box sx={{ flex: 1, mx: 2 }}>{t("adults")}</Box>
              <CounterInput
                min={1}
                max={9}
                value={adults}
                onChange={(count: number) => {
                  setAdults(count);
                  trackEvent({
                    action: "setAdults",
                    label: String(count),
                  });
                }}
              />
            </Box>
            <Box
              sx={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                mb: 1,
              }}
            >
              <Box sx={{ flex: 1, mx: 2 }}>
                {t("children")}
                <div>2-12</div>
              </Box>
              <CounterInput
                min={0}
                max={6}
                value={children}
                onChange={(count: number) => {
                  setChildren(count);
                  trackEvent({
                    action: "setChildren",
                    label: String(count),
                  });
                }}
              />
            </Box>
            <Box
              sx={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                mb: 1,
              }}
            >
              <Box sx={{ flex: 1, mx: 2 }}>
                {t("infants")}
                <div>0-2</div>
              </Box>
              <CounterInput
                min={0}
                max={6}
                value={infants}
                onChange={(count: number) => {
                  setInfants(count);
                  trackEvent({
                    action: "setInfants",
                    label: String(count),
                  });
                }}
              />
            </Box>
          </Box>
          <Divider sx={{ my: 2 }} />
          <Box
            sx={{
              mx: 2,
              mb: 1,
              display: "flex",
              justifyContent: "space-between",
            }}
          >
            <Button
              variant="outlined"
              size="small"
              onClick={handleCancelPassengersMenu}
            >
              {t("misc-cancel")}
            </Button>
            <Button
              variant="contained"
              disableElevation
              size="small"
              onClick={handleClosePassengersMenu}
            >
              {t("misc-apply")}
            </Button>
          </Box>
        </Menu>
      </Box>

      <Box sx={{ display: "inline-block" }}>
        <Button
          aria-label="menu of trip class"
          aria-controls="menu-trip-class"
          aria-haspopup="true"
          color="inherit"
          onClick={handleOpenTypeMenu}
        >
          <Typography variant="button" textAlign="center">
            {tripClass && t(tripClass.i18n)}
          </Typography>
          <ArrowDropDown />
        </Button>
        <Menu
          id="menu-trip-class"
          anchorEl={anchorElType}
          keepMounted
          open={Boolean(anchorElType)}
          onClose={handleCloseTypeMenu}
        >
          {tripClasses?.map((tripC) => (
            <MenuItem key={tripC.id} onClick={() => handleTypeChange(tripC)}>
              <Typography textAlign="center">{t(tripC.i18n)}</Typography>
            </MenuItem>
          ))}
        </Menu>
      </Box>
      <Box component="form" onSubmit={handleSearch}>
        <Stack
          direction={{ xs: "column", lg: "row" }}
          spacing={{ xs: 2, lg: 2 }}
        >
          {/* <Box sx={{ flex: "30%", position: "relative" }}> */}
          <SearchLocation
            initialValue={from}
            placeholder={t("where-from-?")}
            onChange={(v: any) => {
              setFrom(v);
              if (v) {
                trackEvent({
                  action: "setFrom",
                  label: v.code,
                });
              }
            }}
          />
          {/* 
            {userLocation && from?.code !== userLocation.code && (
              <Box
                sx={{
                  position: "absolute",
                  bottom: 0,
                  display: "flex",
                  justifyContent: "center",
                  fontSize: 14,
                }}
              >
                <Chip label="Tip" size="small" sx={{ mr: 1 }} />
                <Link
                  component="button"
                  variant="body2"
                  onClick={() => {
                    setFrom({
                      ...location,
                      city_code: userLocation.code,
                      code: userLocation.code,
                      city_name: userLocation.name,
                      country_name: userLocation.country_name,
                      _type: "city",
                    });
                  }}
                  type="button"
                >

                  {userLocation.name}, {userLocation.country_name}
                </Link>
              </Box>
            )}
            {userLocation && <Box sx={{ py: { xs: 2, lg: 0 } }}></Box>}
          </Box> */}
          <SearchLocation
            initialValue={to}
            placeholder={t("where-to-?")}
            onChange={(v: any) => {
              setTo(v);
              if (v) {
                trackEvent({
                  action: "setTo",
                  label: v.code,
                });
              }
            }}
          />
          {tripType.id === tripTypes[0].id && (
            <DatePickerWithLoclae
              value={[departureDate, returnDate]}
              onChange={([depD, retD]) => {
                setDepartureDate(depD);
                setReturnDate(retD);
              }}
            />
          )}
          {tripType.id === tripTypes[1].id && (
            <>
              <FlightDatePicker
                label={t("departure-date-?")}
                value={departureDate}
                onChange={(newValue: Date | null) => {
                  setDepartureDate(newValue);
                  trackEvent({
                    action: "setDepartureDate",
                    label: String(newValue),
                  });
                }}
                from={from}
                to={to}
                rates={rates}
              />
              {/* <FlightDatePicker
                label={t("return-date-?")}
                value={returnDate}
                minDate={departureDate}
                onChange={(newValue: Date | null) => {
                  setReturnDate(newValue);
                  if (tripType.id !== ETripTypes.round) {
                    setTripType(tripTypes[0]);
                  }
                  trackEvent({
                    action: "setReturnDate",
                    label: String(newValue),
                  });
                }}
                from={from}
                to={to}
                rates={rates}
              /> */}
            </>
          )}
          <Button variant="contained" type="submit">
            <Search />
            {t("home-page-search-btn")}
          </Button>
        </Stack>
        {showBooking && (
          <Box
            sx={{
              display: "flex",
              justifyContent: { xs: "flex-start", lg: "flex-end" },
            }}
          >
            <FormControlLabel
              sx={{
                userSelect: "none",
                mr: 0,
                py: 1,
                "& .MuiFormControlLabel-label": { fontSize: 14 },
              }}
              control={
                <Checkbox
                  size="small"
                  defaultChecked={redirectToBooking}
                  onChange={(e) => {
                    setRedirectToBooking(!!e.target.checked);
                    trackEvent({
                      action: "setRedirectToBooking",
                      label: String(!!e.target.checked),
                    });
                  }}
                />
              }
              label="Check accommodation with Booking.com"
            />
          </Box>
        )}
      </Box>
    </Container>
  );
};
export default SearchForm;
