import { useRouter } from "next/router";
import { useSWRNFetchImmutable } from "./useSwr";

export const useWhoami = ({ shouldFetch = false }) => {
  const { locale } = useRouter();
  const { data, error } = useSWRNFetchImmutable(
    shouldFetch === undefined || shouldFetch === true
      ? `https://www.travelpayouts.com/whereami?locale=${locale}`
      : ""
  );

  return {
    data,
    isLoading: !error && !data,
    isError: error,
  };
};
