import React, { useEffect, useMemo, useRef, useState } from "react";
import Autocomplete from "@mui/material/Autocomplete";
import throttle from "lodash/throttle";
import TextField from "@mui/material/TextField";
import fetchWithRetry from "fetch-retry";
import { useRouter } from "next/router";
import { captureException } from "@sentry/nextjs";

import { retryConfig } from "../utils";
import PlaceIcon from "@mui/icons-material/Place";
import FlightIcon from "@mui/icons-material/Flight";
import Box from "@mui/material/Box";

interface MainTextMatchedSubstrings {
  offset: number;
  length: number;
}

const parseCity = (option: any) => {
  if (option.country_name) {
    return `${option.city_name} - ${option.country_name}`;
  }

  return `${option.city_name}`;
};

const getOptionLabelFn = (option: any) => {
  if (option._type === "airport") {
    return `${option.title} (${option.code}) - ${option.country_name}`;
  }

  if (option._type === "city") {
    return parseCity(option);
  }

  return "";
};

export default function SearchLocation({
  placeholder,
  initialValue,
  onChange = () => {},
}: any) {
  const { locale } = useRouter();
  const [value, setValue] = useState<any | null>({
    description: "INIT_INIT",
  } as any);
  const [inputValue, setInputValue] = useState<string>();
  const [options, setOptions] = useState<readonly any[]>([]);
  const shouldFetch = useRef(false);
  const fetchData = useMemo(
    () =>
      throttle((request, callback: (results?: readonly any[]) => void) => {
        fetchWithRetry(fetch)(
          `https://www.jetradar.com/autocomplete/places?q=${request.input}&locale=${locale}`,
          { ...retryConfig }
        )
          .then((res) => res.json())
          .then((res) => {
            callback(res);
          })
          .catch((error) => {
            if (error?.name !== "AbortError") {
              captureException(error);
            }
          });
      }, 300),
    [locale]
  );

  useEffect(() => {
    let active = true;

    if (inputValue === "") {
      setOptions(value ? [value] : []);
      return undefined;
    }

    // Avoid fetching if we have no input value (any entry to input)
    // Or we have input and initial value equals to that input
    if (
      !inputValue ||
      !shouldFetch.current
      // (inputValue && initialValue && inputValue === parseCity(initialValue)) ||
    ) {
      return;
    }

    fetchData({ input: inputValue }, (results?: readonly any[]) => {
      if (active) {
        let newOptions: readonly any[] = [];

        if (value) {
          newOptions = [value];
        }

        if (results) {
          newOptions = [...newOptions, ...results];
        }

        setOptions(newOptions);
      }
    });

    return () => {
      active = false;
    };
  }, [value, inputValue, fetch, shouldFetch.current]);

  useEffect(() => {
    if (initialValue && value?.description === "INIT_INIT" && !inputValue) {
      setValue(initialValue);
    }
  }, [initialValue, value, inputValue]);

  useEffect(() => {
    if (initialValue?.forceUpdate) {
      const newValue = { ...initialValue };
      delete newValue.forceUpdate;

      setOptions([]);
      setValue(newValue);
      onChange(newValue);
    }
  }, [initialValue]);

  return (
    <Autocomplete
      sx={{ flex: "30%" }}
      blurOnSelect
      size="small"
      aria-required="true"
      getOptionLabel={(option: any) => getOptionLabelFn(option)}
      filterOptions={(x) => {
        return x;
      }}
      options={options}
      autoComplete
      includeInputInList
      filterSelectedOptions
      value={value}
      onChange={(event: any, newValue: any | null) => {
        setValue(newValue);
        onChange(newValue);
      }}
      onInputChange={(event, newInputValue, r) => {
        if (r === "input") {
          shouldFetch.current = true;
        } else {
          shouldFetch.current = false;
        }

        setInputValue(newInputValue);
      }}
      renderOption={(props, option) => {
        const optionString = getOptionLabelFn(option);

        return (
          <li {...props}>
            <Box sx={{ display: "flex", alignItems: "center" }}>
              {option._type === "airport" ? <FlightIcon /> : <PlaceIcon />}
              <Box sx={{ mx: 1 }}>{optionString}</Box>
            </Box>
          </li>
        );
      }}
      renderInput={(params) => (
        <TextField
          {...params}
          ref={params.InputProps.ref}
          variant="outlined"
          fullWidth
          label={placeholder}
          required
          InputLabelProps={{ shrink: true }}
        />
      )}
    />
  );
}
